.post{
    border: 2px solid #ffffff;
    border-radius: 15px;
    max-width: 50%;
    padding: 15px 5px 15px 5px;
    margin: 10px;
    display: flex;
    justify-content: space-around;
}
.post__text{
    background-color: white;
    padding: 10px 25px 10px 10px;
    border-radius: 10px;
    max-width: 40%;
    margin: 0;
}
.post__avatar{
    max-width: 60px;
    max-height: 60px;
    object-position: center;
    object-fit: cover;
    border-radius: 50%;
}
.post__like{}

.post__imgLike{
    width: 15px;
    height: 15px;
}